        // Petici√≥n HTTP GET s√≠ncrona hacia el archivo diario.json del servidor
        req.open("GET", "http://localhost/Pop-proyect/bbdd/crud.php", false);
        // Env√≠o de la petici√≥n
        req.send(null);
